import React from 'react';
import ItemQuestion from '../../components/User/ItemQuestion'
import NavBar from './../../components/NavBar/NavBar';

export default function () {
    return (
        <NavBar title={''}>
            <ItemQuestion title={''} id={'1'}/>
            <ItemQuestion title={''} id={2} />
            <ItemQuestion title={''} id={3} />
            <ItemQuestion title={''} id={4} />
            <ItemQuestion title={''} id={5} />
            <ItemQuestion title={''} id={6} />
        </NavBar>
    )
}



