import React from 'react';
import img from './img/pic.png';
import address from './img/address.png';
import check from './img/check.png';
import radio from './img/radio.png';
import tool from './img/tool.png';
import {Link} from 'react-router-dom';
import NavBar from './../../components/NavBar/NavBar';
import './style.css';
export default function () {
        return(
            <NavBar noNavBar={true}>
                <div className={'desc'}>
                    <img src={img} width="100%" alt={''} />
                    <p></p>
                </div>
                <div className='content-padded'>
                    <nav className="nav-check">
                        <Link to='/listmachine'>
                            <div><img src={radio} width="60" alt={''} /></div>
                            <label></label>
                        </Link>
                        <Link to='/listmarket'>
                            <div><img src={check} width="60" alt={''} /></div>
                            <label></label>
                        </Link>
                        <Link to='/service'>
                            <div><img src={tool} width="60" alt={''} /></div>
                            <label></label>
                        </Link>
                        <Link to='/network'>
                            <div><img src={address} width="60" alt={''} /></div>
                            <label></label>
                        </Link>
                    </nav>
                </div>
            </NavBar>
        )

}


